* {
   font-family: Helvetica, Arial, sans-serif;
}

body {
   padding: 60px;
}

input {
   color: gray;
   font-size: 1.2rem;
   width: 100%;
}

.title{
  text-align: center;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border:1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  text-align: center;
  padding: 5px;
}

.card img{
  max-height: 170px;
}